import React from 'react';
//Components
import ProjectSection from '../components/proj-section';
import {PageIntro} from '../components/page-intro';
import ProjectMeta from '../components/proj-meta';
import Image from '../components/image';


function PostBeam() {
  return (
    <div className="main-wrapper">
      <div className="text-center">
        <PageIntro
          title="Case Study: BeAM"
          tagline="UX Research Project"        
        />
      </div>

	    <section className="project px-3 py-5 p-md-5">

		    <ProjectMeta
          img= "./images/beam/beam-feat.png"
          alttext="Beam Logo"
          role="UX Research"
          industry="Academic"
          timeline = "August 2019 - Dec 2019"
          tools = "Heuristic Analysis, Usability Testing, Interviews"
          description="BeAM, or 'Be a Maker' is University of North Carolina (UNC) at Chapel Hill's network of makerspaces. These makerspaces support students and faculty in designing and making physical objects for education, research, entrepreneurship, and recreation. My team supported BeAM by evaluating their website to provide their users with better resources and an improved user experience."
          req1="Clarify requirements for attending and participating in makerspaces and all related information."
          req2="Make it easy for users to search resources to learn about the equipment and how to use it themselves."
          req3="Users should be able to identify and get in contact with the BeAM staff member(s) if they want more information on a specific workshop and/or tool."
        />


				<div className="project-sections py-5">
          {/*Discovery */}
          <ProjectSection
            title="Discovery"
            headinglevel = "2"
            para={
            <>
              <p>Our first stage of analysis was a heuristic evaluation of BeAM's current website, following <a href="https://www.nngroup.com/articles/ten-usability-heuristics/">Nielsen Norman principles</a>. This was first performed independently by myself and two other researchers on my team, and then we synthesized our analysis to highlight the key issues we discovered in relation to our goals. We found that most principle violations involved a lack of clarity, feedback, and consistency. The following were key issues that we identified: 
              </p>
              <ul>
                <li>Users would have trouble finding resources for operating some of the equipment, such as formatting files correctly.</li>
                <li>Makerspaces are at many different locations on campus and have different services and requirements for participating, which can be confusing.</li>
                <li>Some makerspaces require people to attend an orientation first, but it isnt very clear how attendance works.</li>
                <li>Signing up for a makerspace class lacks a clear path or feedback on whether it was done successfully.</li>
              </ul>
            </>
            }
          /> 

          {/*Findings */}
          <ProjectSection
            title="Usability Testing"
            headinglevel = "2"
            para={
            <>
              <p>This usability study was a somewhat formative assessment, while also incorporating exploratory methods to gain a better understanding of student experiences using the BeAM website. Participants were asked to answer pre-test questions gauging their current level of interest and familiarity with BeAM. They were also asked to think-aloud while completing five tasks, followed by a short post-task questionnaire for each task. Finally, they participated in a post-test semi-structured interview. The order of the tasks shown to participants varied to control for any learning effects that may have taken place. A balanced Latin square was used to develop five task orders. Since only four participants were interviewed, one order was randomly selected to drop. In addition to the qualitative think-aloud and interview data, quantitative metrics focusing on task difficulty and success rates were collected.
              </p>
              <p>The 5 tasks users were asked to complete were:</p>
              <ul>
                <li><b>Find out how to slice a 3D Print File: </b>You have a 3D print due for a class assignment. Using the website please find information on how to correctly prep a 3D file for a 3D printer.</li>
                <li><b>Find locations and hours of a Makerspace: </b>Your teacher is having you do a scavenger hunt for class, where you need to pick up a sticker at each location to show you found it. Item 3 on the list is BeAMs Murray location. Using the website, how would you go about finding this information?</li>
                <li><b>Contact an expert about a BeAM service: </b>You have decided you want to make a birdhouse for your moms upcoming birthday. You have never used a woodshop before but heard the makerspaces on campus have one available to UNC students. Using the BeAM website, find out how you can set up a time to meet with someone to tell you more about the woodshop and help you get started on your project.</li>
                <li><b>Find an orientation time and signup waiver: </b>You need to build a prototype solenoid engine for a class. Your professor mentioned BeAM as a potential resource, and the equipment you need is available at Murray Hall. Find out how to attend an orientation at the Murray Hall Makerspace, so that you can gain access to the materials you need to complete your project.</li>
                <li><b>Register for a workshop:  </b>You want to use Fusion 360 for a collaborative engineering class project but need some help to get started. Find out if you can sign up for a Fusion 360 workshop at the KSL makerspace for the earliest available date.</li>
              </ul>
              <p>The post-task questionnaires were each the following 4 short questions with a 7-point Likert Scale:
              </p>
              <ul>
                <li>The task was easy.</li>
                <li>I am confident that I completed the task.</li>
                <li>This task took a reasonable amount of time to complete.</li>
                <li>The system provided a clear path to complete the task.</li>
              </ul>
            </>
            }
 
                       
          />

          {/*Design Solution*/}
          <ProjectSection
            title="Analysis and Findings"
            headinglevel = "2"
            para={
              <>
                <p>We evaluated the results based on the self-reported post-task questionnaires, interview responses, observations, and how participants navigated to the desired information or service in terms. We did not time participants because we used a think-aloud procedure that would interfere with time taken, but we did track whether participants followed the garden path we determined for finding the information, how many steps they took and how many times they made errors (back-tracked to a previous page/step).</p>
                <br/>
                <p>We first looked at average responses for each of the post-task questionnaires and computed confidence intervals for each. The chart below shows responses to the question on task difficult: </p>
                <br/>
                <Image 
                  img='./images/beam/beam-graph.png'
                  alt='graph of task based response scores'
                  caption='Figure 1: Post-task mean scores and confidence intervals for participant agreement with The task was easy.'
                />
                <br/>
                <p>We calculated lostness for each task, which we based on our proposed garden path (smallest number of screens needed to visit) and errors where users had returned to the same screen again. These results are summarized the the table below.</p>
                <BeamTableOne/>
              </>
            }
          />

          {/*Recommendations and Reflection */}
          <ProjectSection
            title="Recommendations"
            headinglevel = "2"
            para={
            <>
              <p>Based on the results, we determined that most of the problems could be summarized in two main categories: label mismatch, and hidden or missing information. For example:
              </p>
              <ul>
                <li><b>Label mismatches:</b> When looking to set up a consultation with an expert, many participants referred to the People page. Several participants also mentioned confusion surrounding the Events label when it came to participants searching for orientations and workshops. Finally, Technical Resources was not always an obvious choice for participants searching for the 3D printing instructions. </li>
                <li><b>Hidden or missing information:</b> Several participants noted the lack of physical addresses on the location page. When looking to attend a workshop or orientation, it was not always clear how/if they needed to register for the event and what prerequisites may be required (e.g. sign a waiver form or have previously attended an orientation). Additionally, the e-book was not always an obvious choice for participants when searching for the 3D printing instructions, and only one out of five participants found the BeAM staff specialties information on the consultations page. </li>
              </ul>
              <p>Based on the overall results we came up with a set of recommendations that we organized in a cost vs. impact matrix:
              </p>
              <BeamTableTwo/>
            </>
            }
					/>

          {/*Reflection*/}
          <ProjectSection
            title="Reflection"
            headinglevel = "2"
            para={
            <>
              <p>Overall, our usability study was successful in meeting our evaluation goals. The post-task survey results showed that participants felt somewhat confident completing most tasks. Ease of use results were mixed. The difficulty levels of tasks reported by our participants did not always match the completion rates, lostness scores, or qualitative data. Error rates proved to be a difficult metric to collect, since participants discovered new ways to complete tasks which did not match the garden path. Finally, user satisfaction was reported to be somewhat positive as far as how much time it took to complete different tasks, but more negative as far as whether or not the system provided a clear path to do so. By the time we were presenting the project BeAM staff had already implemented some of the low and medium cost changes, which they felt made a meaningful improvement to the website, and had plans to work on our other more time-consuming recommendations.
              </p>
            </>
            }
					/>    



				</div>
	    </section>
    </div>   
  )
}


