import React from 'react';
import Carousel from 'react-elastic-carousel';
//Components
import ProjectSection from '../components/proj-section';
import {PageIntro} from '../components/page-intro';
import ProjectMeta from '../components/proj-meta';
import Image from '../components/image';


function PostHub() {
  return (
    <div className="main-wrapper">
      <div className="text-center">
        <PageIntro
          title="Case Study: 1789Hub"
          tagline="UX Research, Design, and Website Development"        
        />
      </div>

	    <section className="project px-3 py-5 p-md-5">

		    <ProjectMeta
          img= "./images/1789/hub-feat.png"
          alttext="1789Hub Logo"
          role="Research, Design, Development"
          industry="Administration/Business Support"
          timeline = "January 2019 - May 2019"
          tools = "Interviews, Google Analytics, Personas, Rapid Prototyping, Usability Testing"
          description="1789Hub, previously known as 1789 Venture Lab, offers co-working space where students, entrepreneurs, their teams, faculty, mentors, and clubs all come together to make their innovative and entrepreneurial ideas a reality.
          This project involved research, testing and development of a new website for 1789Hub users."
          req1="Understand the needs and goals of the venture lab's existing and potential users."
          req2="Design a user-friendly website that enables or facilitates users in meeting those goals and needs."
          req3="Facilitate connection and collaboration between aspiring entrepreneurs and local start-ups."
        />

				<div className="project-sections py-5">
          {/*Discovery */}
          <ProjectSection
            title="Discovery"
            headinglevel = "2"
            para={
            <>
              <p>Our team of 2 kicked-off with a workshop including various users (students) and organizers involved with the 1789Hub Venture lab. We first gathered information on their values, goals, and assumptions in relation to venture labs in general. We then tried to define their vision, purpose, and pain points with the current lab and its website. This process informed our initial research and design goals. With more background on the project and an understanding of who the users were, we conducted 5 semi-structured interviews of students who used 1789Hub to understand their experiences in greater detail. Our goal was to understand specific goals, interests, and frustrations with the current state of the venture lab and its website. 
              </p>
              <p>Our next step was looking at Google Analytics data from the existing website. Our primary focus was on interaction statistics and issues, such as event sign-ups and contact inquiries. We also requested additional information from the website manager, including event registration to attendee ratios. While they were unable to provide quantitative data on this, they did remark that the ratio was problematic as it made it difficult to schedule workshop hosts. 
              </p>
              <p>We then combined this information together to develop our first set of user personas, summarized as follows:
              </p>
              <ul>
                <li><b>Primary Persona  Aspiring Entrepreneurial Student</b>: As an aspiring entrepreneur, this student aims to use their remaining time at University building their network, honing leadership skills, and getting honest feedback about their venture ideas. They desire mentorship from people who have experienced the ups and downs of starting a business, but reaching out to people alone is not easy. A resource that could enable them and other students with similar goals collaborate with the help of mentors could help them get their foot through the door and beyond.</li>
                <li><b>Secondary Persona  Entrepreneurial Alumnus</b>: This alumnus has had some success starting their own business fairly recently. They feel they could give back to their alma mater and also use it as a source for passionate and talented new hires. However, they are not sure reaching out to past academic mentors will do much good, as they felt academia did not offer them much support in starting their career post-graduation. They would like their alma mater to support more meaningful ways for them to engage with current students to offer mentorship and possible career opportunities.</li>
              </ul>
            </>
            }
          /> 

          {/*Findings */}
          <ProjectSection
            title="Ideation and Analysis"
            headinglevel = "2"
            para={
            <>
              <p>We came up with a set of user stories and then scenarios based on our personas and research findings. We then developed a set of task and interaction requirements for the website, including:
              </p>
              <ul>
                <li>Event search and registration.</li>
                <li>Membership FAQs and sign-up.</li>
                <li>Room reservations.</li>
                <li>Curated listings of local and alumnus-lead internship and job postings. </li>
              </ul>
              <p>We then developed our first iteration of low-mid fidelity wireframes based on the tasks above, in addition to standard web pages you would find on any website. 
              </p>
              <Carousel>
                <Image 
                  img='./images/1789/wf-home.png'
                  alt='1789 Hub homepage wireframe'
                />
                <Image 
                  img='./images/1789/wf-faq.png'
                  alt='1789 Hub faq wireframe'
                />
                <Image 
                  img='./images/1789/wf-internships.png'
                  alt='1789 Hub internships wireframe'
                />
                <Image 
                  img='./images/1789/wf-spaces.png'
                  alt='1789 Hub spaces wireframe'
                />
                <Image 
                  img='./images/1789/wf-events.png'
                  alt='1789 Hub events wireframe'
                />
              </Carousel>
              <p>We tested our wireframes with 9 users on basic navigation tasks and the key interactions previously listed. We also collected feedback on the design following each user session. Feedback on the initial wireframes was positive, with most raised being about functionality we did not include in our mockup, such as including event search tools and an option for startup companies to register their internships or jobs with 1789Hub. We held one more wireframe testing session with 7 different users, then began working on developing solutions for the live site. 
              </p> 
            </>
            }           
          />

          {/*Design Solution*/}
          <ProjectSection
            title="Design Solution"
            headinglevel = "2"
            para={
              <>
                <p>During our wireframe testing we were given positive feedback about our design ideas, but we needed to balance our solutions with some constraints. For desired interactions like event registrations and job postings, they require curation and upkeep to remain useful and user-friendly. As 1789Hub was mostly organized by students, they expected quick turnover for their part-time webmaster role. To address this, we came up with solution requirements that would meet our usability needs in a sustainable manner.</p>
                <br/>
                <h3>Maintain a Flexible and Universal Content Structure</h3>
                <p>Our interviews and usability testing helped us identify information and tasks users were most interested in. We refined the website's information architecture to make sure all interactive tasks would be easy to access and complete. Additionally, we created a content structure that followed universal principles to allow the types of content to be easy to identify. For example, job postings would be entered in a simple form with flexible requirements, which would then be published as a webpage in a consistent structure.</p>
                <br/>
                <h3>Build Solutions Using What People Know</h3>
                <p>No matter how good a website looks at its launch, quality can deteriorate quickly if the tools to update it are poor or difficult to use. In addition to evaluating visitor tasks, we had to be considerate of what the webmaster would need to do to maintain a good user experience. When considering solutions to several features, we first asked if the site manager had anything they were familiar with already that offered the functionality they wanted. The webmaster was already using Google Calendar to manage events, so we decided to embed their event calendar in the website. Google's calendar is more robust and easier to work with than many website plugins, and users were already satisfied with its functionality. It also had the additional benefit of being accessed on 1789Hub's social media platforms and not just the website. 
                </p>
                <br/>
                <h3>Limit Repetition and Prevent Errors</h3>
                <p>With their webmaster's limited bandwidth, our solutions needed to reduce their workload in updated the website or correcting user task errors. The old website did not give users the tools to modify event signups, space bookings, or other interactions, and did not offer much feedback for these tasks. This resulted in the webmaster having to manually correct records frequently and possibly contributed to 1789Hub's poor registration to attendee ratio. </p>
                <p>As one of the requested features was to have curated job listings, we needed to devise a solution that would allow that curation to not become too cumbersome. We created a system where startup recruiters could upload their posting in a simple form, which would then send a notification to the relevant 1789Hub staff and populate a record in their Salesforce database. The staff member could review the submission, make any desired edits, and accept or reject it. We used the automation platform Zapier to upload an accepted job posting to the website in a specified template. This template was assigned a predetermined expiration date so that the postings could be automatically removed in a reasonable time, but this could be manually updated if the position is filled earlier than expected or the recruiter notified that they extended their search.
                </p>
                <Carousel>
                <Image 
                  img='./images/1789/hub-live-home.png'
                  alt='1789 Hub homepage'
                />
                <Image 
                  img='./images/1789/hub-live-member.png'
                  alt='1789 Hub memberships form'
                />
                <Image 
                  img='./images/1789/hub-live-orgs.png'
                  alt='1789 Hub student organizations page'
                />
                <Image 
                  img='./images/1789/hub-live-ventures.png'
                  alt='1789 Hub ventures page'
                />
                <Image 
                  img='./images/1789/hub-live-mentors.png'
                  alt='1789 Hub mentors page'
                />
              </Carousel>
              </>
            }
          />

          {/*Reflection */}
          <ProjectSection
            title="Reflection"
            headinglevel = "2"
            para={
            <>
              <p>Feedback from the student team and management team of 1789 Hub was overwhelmingly positive. Alongside the website launch we put together a set of recommendations to further polish the website and perform post-launch iterations. These recommendations included aligning the embedded Google Calendar with brand guidelines, developing additional graphics, and creating a similar entry format for adding more workshop mentors. Based on the feedback we received, we managed to achieve both our goals of creating a better user experience that meets the needs of students while also developing ways for the 1789Hub student team to sustainably update the website. 
              </p>
            </>
            }
					/>  

				</div>
	    </section>
    </div>   
  )
}

export default PostHub;
