import React from 'react';
import Carousel from 'react-elastic-carousel';
//Components
import ProjectSection from '../components/proj-section';
import {PageIntro} from '../components/page-intro';
import ProjectMeta from '../components/proj-meta';
import Image from '../components/image';


function PostDurham() {
  return (
    <div className="main-wrapper">
      <div className="text-center">
        <PageIntro
          title="Case Study: Durham County Libraries"
          tagline="UX Research"        
        />
      </div>

	    <section className="project px-3 py-5 p-md-5">

		    <ProjectMeta
          img= "./images/dcl/dcl-feat.png"
          alttext="Durham County Library Logo"
          role="UX Research"
          industry="Education"
          timeline = "January 2020 - May 2020"
          tools = "Heuristic Analysis, Usability Testing, Interviews"
          description="In conjunction with a relaunch of the librarys main branch, Durham County Library has initiated a project to redesign their website to better meet patron needs as well as support digital access to their growing set of online resources. This usability testing protocol was intended to determine the extent the website interface facilitates our users ability to complete routine tasks."
          req1="Test and develop an updated information architecture for the website."
          req2="Facilitate discovery of resources and ways to complete tasks that users would otherwise visit a library branch to find or do."
          req3="Future-proof the new website to accomodate potential resources and events that could be made available through the library system."
        />
				<div className="project-sections py-5">

           <ProjectSection
            title="Discovery and Research Protocol"
            headinglevel = "2"
            para={
            <>
              <p>The initial phase was a preliminary review and testing of the existing website to better understand our users and the resources and tasks they would want to find and complete using the library's website. This initial phase involved semi-structured interviews where participants explored the website, as well as tree testing with the existing navigation.</p>
              <p>Using the exploratory research findings, we developed the initial usability testing research protocol for developing wireframes and ultimately a new website. This protocol would define how we would carry out each session, how to recruit participants, and how we could modify the tasks and questions as the project develops.</p>

              <h3>Testing Sessions and Participant Recruitment </h3>
              <p>We first recruited participants for the usability test sessions using an online sign-up form hosted on the existing library website. Additional participants were approached and recruited onsite during test sessions between scheduled participant sessions. In most cases participants did show up to their scheduled testing session on time. In cases where participants did not show up on time, they were given a 15-minute grace-period before we searched for a participant to recruit. Each individual session of usability testing lasted 20 to 30 minutes. During the session, the test administrator explained the test session and asked the participant to answer brief background questionnaire (see Attachment A). After participants agreed to the terms and to have their session screen and voice recorded, they were handed a slip of paper with the first task description. The test administrator also read the task slip out loud. A total of 8 Task slips were randomly shuffled before each session to control for order-effects.</p>

              <h3>Evaluation Tasks/Scenarios</h3>
              <p>For each session, test participants attempted completion of 8 out of the following 10 tasks:</p>
              <ol type="A">
                <li>Find a library location and hours.</li>
                <li>Find how to use library printers.</li>
                <li>Find resources for teenagers at the library.</li>
                <li>Find how to request an interlibrary loan.</li>
                <li>Find out how to get a library card.</li>
                <li>Find out how to reserve a study space.</li>
                <li>Find out how to access non-paywall New York Times articles.</li>
                <li>Find resources on a specific research topic.</li>
                <li>Find out how to check out e-books.</li>
                <li>Find out how to use the library catalog to search for a specific genre.</li>
              </ol>
              <p>Scenarios based on these tasks were presented to participants in a random order, and each participant was asked to listen to the test administrator who read the task out loud, but was also given a slip of paper with the task scenario printed on it.</p>

              <h3>Post-Task and Post-Session Protocol</h3>
              <p>After each task, the administrator asked the participant to rate the interface on a 7-point Likert Scale with measures ranging from Strongly Disagree to Strongly Agree. Post-task scenario subjective measures included:</p>
              <ul>
                <li>How easy it was to find the information from the home page.</li>
                <li>Confidence in task completion.</li>
                <li>How time-consuming participants felt the task was.</li>
                <li>Accurateness of predicting which section of the website contained the information.</li>
              </ul>
              <p>After the last task was completed, the test administrator asked the participant to describe their experience with the website overall, based on the following subjective measures:
              </p>
              <ul>
                <li>Ease and difficulty of use. </li>
                <li>Most and least challenging task.</li>
                <li>Sense of surprise or frustration about navigation of site content.</li>
                <li>What the participant liked most.</li>
                <li>What the participant liked least.</li>
                <li>Recommendations for improvement.</li>
              </ul>
            </>
            }
          />

          <ProjectSection
            title="Testing Phase 1: Wireframes "
            headinglevel = "2"
            para={
            <>
              <p>We made the most changes to the protocol and task list during wireframe iterations as we discovered which tasks were most relevant to users during post-session interviews.</p>
              <Carousel itemsToShow={1}>
                <Image
                  img='./images/dcl/dcl-wf-home.png' 
                  alt='wireframe of library homepage'
                />
                <Image
                  img='./images/dcl/dcl-wf-location.png' 
                  alt='wireframe of library location page'
                />
                <Image
                  img='./images/dcl/dcl-wf-resources.png' 
                  alt='wireframe of library online resourcespage'
                />
                <Image
                  img='./images/dcl/dcl-wf-card.png' 
                  alt='wireframe of library card page'
                />
                <Image
                  img='./images/dcl/dcl-wf-account.png' 
                  alt='wireframe of library account login page'
                />
                <Image
                  img='./images/dcl/dcl-wf-ebook.png' 
                  alt='wireframe of library ebook checkout page'
                />
              </Carousel>
              <p>Overall for this phase, there was a total of 29 for onsite usability tests, ranging from 5-6 participants at each site. Test duration per participant in the phase ranged from 20-30 minutes. Each participant first answered pre-test questions about their experience with the library, its website, and technology, followed by 8 tasks on the website each with post-task questions, and ending with a final short interview for overall feedback. After each session we made minor iterative changes to the wireframes to test navigation improvements. We found that only minor changes were needed to the navigation and arrangement of information on certain pages. Quantitative results for task completion rates for this phase is shown in the table below:</p>
              <DclTableOne/>
            </>
            }
          />

          <ProjectSection
            title="Testing Phase 2: First Click Tests "
            headinglevel = "2"
            para={
            <>
            <p>Before testing with the staging website, we conducted 60 first-click test sessions over one day using Optimal Workshop. The purpose of this test was to find whether visual elements impact the visibility of elements on the homepage that can simplify navigation, such as a common tasks section. Our task list was modified as follows:</p>
            <ol type="A">
              <li>Where would you first click to find New York Times articles?</li>
              <li>Where would you first click to find resources for teens?</li>
              <li>Where would you first click to find your account login?</li>
              <li>Where would you first click to find language learning resources?</li>
              <li>Where would you first click to find library card information??</li>
              <li>Where would you first click to find something in the catalog?</li>
              <li>Where would you first click to find an e-book?</li>
              <li>Where would you first click to find a specific location and hours?</li>
              <li>Where would you first click to find how to book a study room?</li>
            </ol>

            <p>Overall, tasks D, E and H had poor results, suggesting either a need for modifying the task scenario prompt or location of these sources. We did not find a significant difference in success or use of homepage elements between this test and the wireframe testing. Average results of first-click success rate and time taken to complete are compiled in Table 2 below: </p> 
            <DclTableTwo/>
            </>
            }
          />

          <ProjectSection
            title="Testing Phase 3: Staging Site "
            headinglevel = "2"
            para={
            <> 
              <p>With the staging site, we returned to testing at library locations following a similar research protocol to the one used during wireframe testing. Fewer tasks were used due to certain areas of the website being under development. Our modified task list included tasks A, B, C, F, and G (same letter and scenario association as the above wireframe tasks). We had a total of 12 participants at two branch locations. Testing was halted due to the Covid-19 pandemic, but our limited set of results showed that the design decisions made in the previous sessions proved to be effective on a functional website. </p>
              <p>Tasks C (interlibrary loans) and F (finding library card) were the most challenging for users, which was consistent with wireframe testing. This is still likely to do with the library card prompt we used being difficult to frame without making the task too obvious (i.e. we suggested they join the library rather than directly state to find how to get a card). For interlibrary loans, it was likely due to the service not being known to most users, and we also did not use the term interlibrary loan directly but asked users to find how to get a book that was not in physical or digital catalogs. Despite not completing the task directly, many users were still able to find alternative services or options to complete this task, such as requesting the library purchases a book. While testing was limited in comparison to the previous session, this was the first time in our testing process where any task had a 100% completion rate.</p>
              <DclTableThree/>
            </>
            }
          />

          

          <ProjectSection
            title="Analysis and Recommendations"
            headinglevel = "2"
            para={
            <>
              <p>Most participants reported that they found the experience positive and that most tasks were easy. Those who had experience with the existing website said they felt the new design was easier to navigate and much simpler. Some participants did note that the lack of design elements in the wireframes and some of the staging site made it difficult to judge how easy it would be to use a fully developed website. Many participants felt that the navigation was comprehensive without being too cluttered and offered multiple logical paths to reach the desired information. Some participants emphasized a few areas they wanted the website to develop carefully in the future:</p>
              <ul>
                <li>Mobile friendliness was important to several participants who noted that the existing site had some issues on mobile.</li>
                <li>Some participants felt that the site could do a better job of letting them know what resources are available to them.</li>
                <li>Some participants emphasized the need for Spanish language content.</li>
              </ul>
              <p>In conclusion, Most of the participants found the redesign of durhamcountylibrary.org to be well-organized, comprehensive, clean and uncluttered, very useful, and easy to use. Having a single site that provided easy access to all the resources the library offers with minimal steps was key to many participants. Continued work on this project in areas such as mobile testing, developing and testing Spanish content, and testing of the complete designed site will ensure a continued user-centered website.</p>
              <p>Pages from the design resulting from these recommendations are shown below.</p>
            </>
            }
          />

          <Carousel>
            <Image
                    img='./images/dcl/dcl-live-home.png' 
                    alt='live homepage for the durham county libraries website'
            />
            <Image
                    img='./images/dcl/dcl-live-catalog.png' 
                    alt='live catalog page for the durham county libraries website'
            />
            <Image
                    img='./images/dcl/dcl-live-events.png' 
                    alt='live events page for the durham county libraries website'
            />
            <Image
                    img='./images/dcl/dcl-live-resources.png' 
                    alt='live resources page for the durham county libraries website'
            />
            <Image
                    img='./images/dcl/dcl-live-account.png' 
                    alt='live account page for the durham county libraries website'
            />
            <Image
                    img='./images/dcl/dcl-live-ask.png' 
                    alt='live contact page for the durham county libraries website'
            />
          </Carousel>



				</div>
	    </section>
    </div>   
  )
}

