import React from 'react';
//Components
import ProjectSection from '../components/proj-section';
import {PageIntro} from '../components/page-intro';
import ProjectMeta from '../components/proj-meta';


function PostClinicloud() {
  return (
    <div className="main-wrapper">
      <div className="text-center">
      <PageIntro
        title="Case Study: Clinicloud"
        tagline="UX Research"        
      />
      </div>

	    <section className="project px-3 py-5 p-md-5">

		    <ProjectMeta
          img= "./images/clinicloud/clinicloud-feat.png"
          alttext="Clinicloud logo"
          role="UX Research"
          industry="Healthcare"
          timeline = "August 2019 - December 2019"
          tools = "Heuristic Analysis and Usability Testing"
          description="This project was completed for a course at UNC Chapel Hill in Human Factors in System Design (INLS 418) with one other student. The goal of our project was to compare the usability of Clinicloud, a mobile-application supported healthcare device with a stethoscope and non-contact thermometer, to similar devices. We compared the sthetoscope usabily and application support of Clinicloud to the Littmann Electronic Stethoscope, and the thermometer to the Koogeek Smart Thermometer."
          req1="Perform a heuristic analysis of each device following principles modified for medical device applications."
          req2="Develop recommendations for medical devices and their mobile application support."
        />

				<div className="project-sections py-5">

          <ProjectSection
            title="Discovery"
            headinglevel = "2"
            para={
            <>
              <p>The first phase of this project was performing a heuristic evaluation (i.e. expert usability evaluation) of the devices and applications and researching the intended users of the devices to develop personas and task scenarios to inform how to conduct usability research. Researching users was largely done by reviewing what the intended users for each device are from the manufacturers, as well as looking at user reviews on websites like Amazon. Determining the heuristic evaluation to use was more complicated because we needed a set that covered both the physical device issues and mobile applications. We used <a href="https://www.sciencedirect.com/science/article/pii/S1532046403000601">Zhang's 14 Heuristics</a> for evaluating medical devices, as it incorporated elements of interface heuristics such as <a href="https://www.nngroup.com/articles/ten-usability-heuristics/">Nielsen Normans</a> along with additional considerations for physical devices and healthcare. Myself and my colleague rated the each devices separately and then compared results. Each device was given a rating from 0 to 3, where 0 meant there were no issues and 3 represents issues that must be addressed to be useful/functional. Below is a sample of the results for the first 4 heuristics:
              </p>
              <ClinicloudTableOne/>
              <p>We also did some initial user testing sessions with all three of the devices and their applications. We had 4 tasks:</p>
              <ol>
                <li>Use Clinicloud to record temperature. </li>
                <li>Use Clinicloud to record heart rate. </li>
                <li>Use Koogeek to record temperature. </li>
                <li>Use Littmanns Stethoscope to examine heart rate. </li>
              </ol>
              <p>Data was collected on task performance (completion, screens visited, errors), post-task questionnaire responses, post-session open-ended questions as well as observations.</p>
            </>
            }
          /> 

          <ProjectSection
            title="User Testing"
            headinglevel = "2"
            para={
            <>
              <p>Following our heuristic analysis, we did some initial user testing sessions with all three of the devices and their applications, for a total of four tasks. For each task, data was collected on task performance (completion, screens visited, errors), post-task questionnaire responses, post-session open-ended questions, as well as observations. Our tasks were:</p>
              <ol>
                <li>Use Clinicloud to record temperature. </li>
                <li>Use Clinicloud to record heart rate. </li>
                <li>Use Koogeek to record temperature. </li>
                <li>Use Littmanns Stethoscope to examine heart rate. </li>
              </ol>
              <p> From the heuristics and user testing, some of the key issues we found overall was that instructions to do task were difficult to find in most systems, especially Clinicloud as it offered two separate functions. Aside from Littmann which is marketed towards practitioners, the home healthcare devices dont really offer enough details or feedback to troubleshoot when results dont appear correct. One issue for Clinicloud specifically is that it lacked a meaningful hierarchy to its functions on the mobile application, It had several buttons for recording data from devices such as heart rate monitors that the system did not have, which were given the same emphasis as their core thermometer and stethoscope functions. The results from user testing supported these findings, as users were not very confident they were doing the right thing and did not find enough information or helpful feedback.</p>
            </>
            }
          /> 

          <ProjectSection
            title="Recommendations"
            headinglevel = "2"
            para={
            <>
              <p>Based on the findings of this study, we came up with 3 key recommendations for designing home-healthcare devices that integrate different functions:</p>
              <ul>
                <li><b>Ease of Use vs. Accuracy of Use:</b> Often applications designers push for simple diagrams or sets of instructions in order to not overwhelm users with details, but when too many details are missing the device is rendered ineffective. Device designers should aim to offer users all necessary details to operate the device with accuracy, and only hide detailed information that is not necessary. </li>
                <br/>
                <li><b>Answer the Users Key Questions:</b> Users purchase home healthcare applications primarily to answer the question: Am I healthy? Home healthcare applications should do their best to offer users feedback that compares their test results to established data on what is considered a healthy reading based on the users profile. </li>
                <br/>
                <li><b>Dont Over-rely on Mobile Applications:</b> Users purchase a home healthcare device for the physical device, so the application should be a bonus to assist the users in tracking past information, easily inputting a user profile and other features such as connecting the device results to other health applications. </li>
              </ul>
            </>
            }
          />
					     
				</div>
	    </section>
    </div>   
  )
}

function ClinicloudTableOne() {
  return (
    <table className="table">
    <caption>Table 1: Zhangs Heuristics 1-4 for each of 3 devices. </caption>
      <thead>
        <tr>
          <th >Heuristic</th>
          <th >Clinicloud</th>
          <th >Koogeek Thermometer</th>
          <th >Littmann Stethoscope</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td ><b>Consistency</b></td>
          <td >0  theme remains consistent in app and aligns with device</td>
          <td >1  each app page has a different appearance but still aligns with device function</td>
          <td >0  theme remains consistent in app and aligns with device</td>
        </tr>
        <tr>
          <td ><b>Visibility</b></td>
          <td >0  uses color coding to distinguish features</td>
          <td >2  user knows where they are at, but no feedback to explain what they are looking at</td>
          <td >0  has many words and directions to alerts user of what they are looking at</td>
        </tr>
        <tr>
          <td ><b>Match</b></td>
          <td >0  application has images and colors to match. Icons signify  real world representations</td>
          <td >1  has images for the user to have to assume what it is, limited description.</td>
          <td >0  has images and colors to match word descriptions and real world representations</td>
        </tr>
        <tr>
          <td ><b>Minimalism</b></td>
          <td >2  Has 8 features organized in buttons, only 2-3 of which reflect the key device function.</td>
          <td >2  only has 4 buttons but it is hard to assume what each are and has many pop up features.</td>
          <td >2 - The application offers many resources that are difficult to navigate through and have an unclear hierarchy.</td>
        </tr>
      </tbody>
    </table>
  )
}

export default PostClinicloud;
