import React from 'react';
//Components
import {PageIntro} from '../components/page-intro';
import Blog from '../components/blog';

function Publications() {
  return (
    <div className="main-wrapper"> 
        <div className="text-center">
            <PageIntro 
            title="Publications"    
            />
        </div>
        <section className="blog-list px-3 py-5 p-md-5">
		    <div className="container single-col-max-width">
                <Blog
                    image="./images/publications/stakeholders.jpg"
                    alt="A researcher communicating her findings to her team of stakeholders using a whiteboard"
                    title="How to Communicate Your UX Findings to Stakeholders"
                    intro="In this blog post, I share my experience an insights into best strategies for communicating UX research findings with all stakeholders."
                    date="02/22/2022"
                    type="Blog Post"
                    publisher="Userlytics"
                    link="https://blog.userlytics.com/how-to-communicate-your-ux-findings-to-stakeholders/"
                />
                <Blog
                    image="./images/publications/academic.jpg"
                    alt="Screenshot of an accessibility evaluation of a website using Deque's evaluation tool"
                    title="Accessibility of public library websites in the United States"
                    intro="This study sought to assess how accessible public library websites are to patrons with disabilities, understand the types of accessibility errors that library websites have, and investigate the relationship between library website accessibility and community demographics libraries serve."
                    date="01/30/2022"
                    type="Academic Paper"
                    publisher="Journal of Universal Access in the Information Society"
                    link="https://link.springer.com/article/10.1007/s10209-022-00866-y"
                />
                <Blog
                    image="./images/publications/information-architecture.png"
                    alt="Venn Diagram of the key elements of Information Architecture"
                    title="How to Build a Solid Website Information Architecture"
                    intro="In this blog post, I share my approach to building information architecture for a website, including both principles and processes to follow."
                    date="11/29/2021"
                    type="Blog Post"
                    publisher="Userlytics"
                    link="https://blog.userlytics.com/how-to-build-a-solid-website-information-architecture/"
                />
                
            </div>
        </section>
    </div>
  );
}

export default Publications; 