import React from 'react';
//Components
import ProjectSection from '../components/proj-section';
import {PageIntro} from '../components/page-intro';
import ProjectMeta from '../components/proj-meta';
import Image from '../components/image';

function PostDmrf() {
  return (
    <div className="main-wrapper">
      <div className="text-center">
        <PageIntro
          title="Case Study: DMRF Canada"
          tagline="UX Research"        
        />
      </div>

	    <section className="project px-3 py-5 p-md-5">

		    <ProjectMeta
          img= "./images/dmrf/dmrf-feat.png"
          alttext="DMRF Canada Logo"
          role="Research"
          industry="Not for Profit (Medical)"
          timeline = "January 2017 - June 2017"
          tools = "Tree Testing, Usability Testing"
          description="The Dystonia Medical Research Foundation Canada (DMRF Canada) is a not-for-profit dedicated to advancing research for more treatments and ultimately a cure; to promote awareness and education; and to support the needs and well being of individuals living with dystonia, a neurological movement disorder. I began working with them as a development coordinator where I pitched a project to make their website more user-friendly and best serve people with dystonia and their family."
          req1="Revise the structure of content to better suit the needs and mental model of our users"
        />

				<div className="project-sections py-5">
          {/* Information Architecture*/}
          <ProjectSection
            title="Discovery"
            headinglevel = "2"
            para={
            <>
              <p>I began this project with an information architecture audit. The first step to this was mapping out the original navigation menu, followed by mapping and auditing all content, whether linked from the menu or not. After mapping all the existing content, I developed the following simple personas to have in mind while walking through the content to identify gaps and issues: 
              </p>
              <ul>
                <li><b>Primary Persona  Person with Dystonia (PwD)</b>: Their interests in this website include support group events, patient resources, and news. They expect resources to be more comprehensive than other websites, with both information and actionable content. For example, they would expect to read about up to date news on treatments from DMRF Canada's funded researchers, as well as search for relevant local healthcare contacts or assistive device resources. </li>
                <li><b>Secondary Persona  Family/Friend of PwD</b>: They are interested in family or caretaker support resources or events, as well as ways to help the foundation through donation, fundraising, and other efforts. Their goal is to provide informed and meaningful support to their loved one(s).</li>
                <li><b>Secondary Persona  Medical Professional</b>: They are interested in funding opportunities for their dystonia research and opportunities to use DMRF Canada's platform to spread awareness of new developments in dystonia patient care and research. </li>
              </ul>
              <p>
                I navigated through the existing content as each of the three personas, which aided me in identifying the following key issues with the website's information architecture: 
                </p>
                <ul>
                  <li><b>No Clear Audience: </b>Areas where the navigation addressed a specific audience did not offer relevant content, and content addressed to a specific audience lacked a clear path in the navigation and website architecture.</li>
                  <li><b>Lack of Clarity: </b>There was a lack of clarity and mismatch between what navigation menu items and the content they linked to. For example, there was a main navigation link titled 'Support', which was ambiguous as to whether it meant support for people with dystonia or ways to support the foundation.</li>
                  <li><b>Content Bloat and Redundancy: </b> While the website was being updated with new content rather frequently, there was a clear lack of maintenance to consolidate or remove anything incorrect or dated. This created a great deal of overlapping and redundancy, with some information being inconsistent across different pages, and therefore unreliable for visitors.</li>
                </ul>
            </>
            }
          /> 

          {/*Findings */}
          <ProjectSection
            title="Ideation and User Testing"
            headinglevel = "2"
            para={
            <>
              <p>I began the ideation phase with a brainstorming session where I annotated a print-out of the existing menu structure with potential issues. The goal of this phase was to address some low-hanging fruit issues that violated usability and information architecture principles, before diving into usability testing.
              </p>
              <Image 
                  img="./images/dmrf/oldmenu-3.png"
                  alt="Breakdown of existing menu"
              />
              <br/>
              <p>
              What stuck out initially was that the navigation had a great deal of nesting and categories that overlapped in meaning while not adequately encompassing the many things the site had to offer. Below are some specific issues I had highlighted about the main-navigation sections:
              </p>
              <ul>
                <li><b>What is Dystonia: </b>This section had an enormous amount of nested sub-sections about dystonia, treatments and other information pieces that could be consolidated into a smaller number of pages or other forms of content. This oversized nested navigation with 3 levels of subheadings was especially troublesome for mobile use. Despite being much larger than it should have been, it was also missing some useful content, such as sections of symptoms and diagnosis of dystonia, or a general FAQ page. </li>
                <li><b>Research: </b>This section included some research news targeted towards dystonia patients and family as well as resource groups for medical researchers. However, some resources for researchers, such as funding  opportunities, were listed under the "Professional" section, which offered little value aside from a page on funding opportunities. </li>
                <li><b>Resources: </b>There was a great deal of content under this section but it was all presented in a pdf format in a "patient package". There was potential to break this section down into key types of resources targeting different groups (e.g. patients, caregivers, parents, etc) or needs (e.g. financial, medical, wellness, etc).</li>
                <li><b>Support: </b>This section title was ambiguous, as 'support' could refer to supporting the organization or support for patients. Either option appeared to overlap with other navigation sections ('ways to help' for helping the organization, or 'resources' for patient support). The actual purpose of this section was to showcase local and online support groups and events.</li>
              </ul>
              <br/>
              <h3>Open Card Sorting</h3>
              <p>Before developing a draft of an updated menu structure, I recruited 20 participants from our volunteer network to participate in a card sort of items in the second level of navigation. This was an open card sort, meaning that participants were provided with the titles of second level navigation items and were asked to group them together and assign their own category name to each group. The purpose of this study was to uncover our user's mental model and aid in developing a new structure for further testing. The categorization themes uncovered were:</p>
              <ul>
                <li><b>Administrative Information:</b> Basic information about the organization, staff, contacts, and other administrative information. This was the kind of content you would typically see in an "About Us" section and participant mental models did not deviate much from what was on the existing site for this section.</li>
                <li><b>Patient Informational Resources:</b>Participants overwhelmingly grouped content together that served as informational resources for patients and caregivers. This included information about dystonia as a condition, treatment options, and more. </li>
                <li><b>Patient Transactional/Support Resources: </b>Participants overwhelmingly separated the information resources from more actionable resources, such as support group network information, doctor network information, events, and more.  </li>
                <li><b>Researcher Resources:</b>Participants distinguished resources available to medical researchers, such as grant applications, from resources made for patients and caregivers.</li>
                <li><b>Volunteer Resources:</b>Participants grouped all content related to volunteering, donating, and otherwise supporting the organization, into its own category.</li>
              </ul>
              <br/>
              <h3>Closed Card Sorting and Tree Testing </h3>
              <p>After conducting the open card sort, I developed a first draft for a possible new structure, which I reviewed with my supervisor to get feedback and come up with additional revisions. I conducted a closed card sort to make sure the level 1 labels selected made sense to users and uncover any gaps.</p>
              <p>Results from the closed card sort was consistent with the open card sort, so I then used the Optimal Workshop Tree-testing tool to recreate my second draft of the menu navigation and created the following 3 third-person task scenarios for our participants to walk-through, with their respective intended choices:
                </p>
                <ul>
                  <li>You are a patient with dystonia and want to find other dystonia patients in your area to connect with.</li>
                    <ul><li>Intended Choice: Community = Local Support Groups </li></ul>
                  <li>You are a medical researcher interested in dystonia and want to find grants or other funding opportunities to support your research.</li>
                    <ul><li>Intended Choice: Research = Research Funding Opportunities </li></ul>
                  <li>You are the parent of a child who has just been diagnosed with dystonia and want to find resources on how you can best support your child.</li>
                    <ul><li>Intended Choice: Resources = Caregiver Resources </li></ul>  
                </ul>
                <p>The testing link was sent out to people within our support group network and I received a total of 16 responses. For the first task where participants looked for support groups, all participants succeeded in making the intended choice, though two had first attempted to check Online Support Groups before visiting Local Support Groups. For the second task, where participants looked for research funding, there was complete direct success. The third task had the most difficulty, where 10 patients directly succeeded, but 5 succeeded indirectly and 1 indirectly failed. Participants indicated in follow up survey responses that they couldnt find the word parent anywhere, so this took them some more time to figure out.</p>
                <Image 
                  img="./images/dmrf/finding-2.png"
                  alt="Breakdown of existing menu"
              />
              </>
            }
          />

          {/*Design Solution*/}
          <ProjectSection
            title="Design Solution"
            headinglevel = "2"
            para={
              <>
                <p>With the second draft we found that overall our visitors found the navigation easy to use and that they could find the information they were tasked to look for quickly. Some key changes made for this final solution were:
                </p>
                <ul>
                <li>Eliminating all 3rd level menu items and consolidated them within other sections.</li>
                <li>Consolidating the Research and Professional sections to target medical professionals and people interested in dystonia research developments.</li>
                <li>Renaming the 'Support' section to 'Community' and clarifying sub-items to be more clearly about support groups for people with dystonia.</li>
                <li>Using simple subheadings for resources that address specific kinds of needs or interests.</li>
                </ul>
                <Image 
                  img="./images/dmrf/menu-final.png"
                  alt="Newly designed menu structure"
              />
              </>
            }
          />


          {/*Reflection */}
          <ProjectSection
            title="Reflection"
            headinglevel = "2"
            para={
            <>
              <p>Much of this project involved consolidating a large number of sub-sections into one section that could link to all the different pages. I did additional work to create new pages to link to that information in a way that wouldnt appear too overwhelming. I also had to address some ambiguities in navigation titles, such as Support representing the support group section. I believe I was able to come up with elegant solutions to the navigation, which was supported by the tree test results where participants always succeeded directly when selecting the main sections. Overall, as part of my first information architecture project, I believe this was a success.</p>
              
            </>
            }
					/>  

				</div>
	    </section>
    </div>   
  )
}

export default PostDmrf;
