import React from 'react';
import {Link} from 'react-router-dom';

/*Props:
img, title, description, client, link

*/

function Card(props) {
  return (
  	<div className="col-md-6 mb-5">	
			<div className="card project-card">
				<div className="row no-gutters">
					<div className="col-12 col-xl-5 card-img-holder">
						<img src={props.img} className="card-img" alt={props.alt}/>
					</div>
					<div className="col-12 col-xl-7">
						<div className="card-body">
							<h5 className="card-title">{props.title}</h5>
							<p className="card-text">{props.description}</p>
							<p className="card-text"><small className="text-muted">Client: {props.client}</small></p>
						</div>
					</div>
				</div>
				<div className="link-mask">
					<Link className="link-mask-link" to={props.link}></Link>
          <div className="link-mask-text">
						<Link className="btn btn-secondary" to={props.link}>
							<i className="fas fa-eye me-2"></i>View Case Study
						</Link>
					</div>
				</div>
		</div>
  </div>
  );
}

export default Card;