import React from 'react';


/*props 
-image
-alt
-title
-intro
-link
*/

function Blog(props) {
    return (
        <>
            <div className="row g-3 g-xl-0">
                <div className="col-2 col-xl-3">
                    <img className="img-fluid post-thumb" src={props.image} alt={props.alt}></img>
                </div>
                <div className="col">
                    <h3 className="title mb-1"><a class="text-link" href={props.link}>{props.title}</a></h3>
                    <div class="meta mb-1">
                        <span class="date">{props.date}</span>
                        <span class="time">{props.type}</span>
                        <span class="comment">Published by {props.publisher}</span></div>
                    <div className="intro">{props.intro}</div>
                    <br/>
                </div>
            </div>
            <br/>
        </>
    );
  }

export default Blog;