import React from 'react';
//Components
import ProjectSection from '../components/proj-section';
import {PageIntro} from '../components/page-intro';
import ProjectMeta from '../components/proj-meta';
import Image from '../components/image';


function PostEarly() {
  return (
    <div className="main-wrapper">
      <div className="text-center">
        <PageIntro
          title="Case Study: EarlySense Live"
          tagline="UX Research"        
        />
      </div>

	    <section className="project px-3 py-5 p-md-5">

		    <ProjectMeta
          img= "./images/earlysense/earlysense-feat.png"
          alttext="Early sense live logo"
          role="UX Research"
          industry="Healthcare"
          timeline = "August 2018 - December 2018"
          tools = "Heuristic Analysis, Task Analysis, Usability Testing"
          description="As part of a project for a course I took in User Interface Design (INLS718) at the University of North Carolina at Chapel Hill, myself and two fellow students completed an evaluation project of the seemingly discontinued EarlySense Live Sleep Monitor Application. The EarlySense Live application is used in conjunction with a physical sensor device that is placed underneath your mattress. The device analyzes sleep cycles and monitors health while the user sleeps. It sends real-time updates of descriptive statistics about sleep stages, heart rate, breathing rate, stress level and movement to the application as you sleep. EarlySense was originally created to better support general care patients during their hospital stays but has since been used and marketed outside of hospitals to health-conscious adults, parents, and caretakers of senior citizens."
          req1="Make it easier and more intuitive for users to monitor the changes in their sleep quality over time."
          req2="Allow users to find information about their vitals and sleep metrics easily and communicate that information in a manner that they can understand."
          req3="Simplify navigation throughout the application so that users can find the information they need."
        />

				<div className="project-sections py-5">

          <ProjectSection
            title="Discovery"
            headinglevel = "2"
            para={
            <>
              <p>Preliminary issues were identified through heuristic analysis, reviewing customer product reviews on websites like Amazon, and conducting exploratory usability studies. We then identified the following key issues:
              </p>
              <ul>
                <li>Users have limited means to compare changes in their sleeping patterns or other health metrics over time in a way they can make sense of.</li>
                <li>Metrics, especially those related to sleep, were poorly explained and defined.</li>
                <li>The formula for scoring sleep was unclear in both what a good score means and how it was calculated and displayed graphically.</li>
                <li>Interacting with charts often caused scroll hijacking as they were not built in a mobile-friendly manner, despite being a mobile application.</li>
                <li>The homepage did not display any useful or relevant information.</li>
              </ul>
              <p>After our initial review of the product and preliminary research, we developed personas, essential use cases, user scenarios and the tasks they would complete, and a hierarchical task analysis for each use case. The purpose of doing this was to identify which of the issues weve witnessed could have the most impact on how user-friendly the device is to their target audience. </p>
              <p>We have developed 4 personas, 2 primary and 2 secondary personas, based on both the analysis of users from EarlySense, as well as an analysis of common themes across customer reviews on Amazon. The personas can be further divided into 2 types: those who want the device for their own use, or those who want it to monitor others, where there is a primary and secondary persona for each type. For more details on the above personas, you can read pages 7 through 10 in the <a href="https://hsl.lib.unc.edu/wp-content/uploads/2019/01/EarlySense_mobileApp.pdf">project report</a>.</p>
              <p>We identified several use cases that the application had, from entering information in a diary to tracking sleep data. After conducting a hierarchical task analysis we ranked which use cases were the most essential for this application, which mostly involved tracking vitals, reviewing changes in sleep over time, and adjusting settings. Scenarios were developed for each essential use case based on the relevant personas.</p>
              <Image
                  img='./images/earlysense/earlysense-hta.png' 
                  alt='Hierarchical Task analysis of original early sense live application'
                  caption='Sample hierarchical task analysis diagram for diary keeping on the EarlySense Live application'
                />
            </>
            }
          /> 

          <ProjectSection
            title="Ideation"
            headinglevel = "2"
            para={
            <>
              <p>Based on the tasks we identified and preliminary research, we worked on 6 key areas to improve usability on the application. 
              </p>
              <ol>
                <li>Allow users to compare data from different days side-by-side.</li>
                <li>Provide explanations of metrics in the sleep summary tabs.</li>
                <li>Modify the sleep score formula and graphical display to be more clear.</li>
                <li>Add a way to expand charts and interact with them to avoid scroll hijacking.</li>
                <li>Update the homepage to have more relevant information.</li>
                <li>Display sleep information first on metrics sidebar. </li>
              </ol>
              <p>More detailed information on all the above issues can be found on pages 23 to 29 in the <a href="https://hsl.lib.unc.edu/wp-content/uploads/2019/01/EarlySense_mobileApp.pdf">full report</a>. For a sample of our analysis, Ive summarized issue #5 as follows:</p>
              <p>The existing homepage only displayed whether or not the sensor was connected, if the user was in bed, showed live tracking and had a smart wake-up alarm switch to enable that supposedly would sound within a time-frame when the user was in an appropriate phase of sleep. The live tracking aspect may have made some sense from the point of view of a parent tracking a child or a healthcare practitioner tracking a patient, but for somebody using this device themselves it did not seem useful at all as they would be asleep as its running. Even for a parent or healthcare practitioner, if there was a need to closely track vitals in a child or patient they would likely want to use more precise tools that have an alarm system for irregular metrics. 
              </p>
            </>
            }                
          /> 

          <ProjectSection
            title="User Testing"
            headinglevel = "2"
            para={
            <>
              <p>The redesigned homepage screen is one example of a change we implemented in the modified application prototype. We developed other screens and tasks based on the 6 key issues we identified and then tested each of these tasks with 4 users following the testing script that can be found on pages 39-40 of the final report. We compared the results of usability testing to what we found in our preliminary research, and while there were many areas that improved, we identified some weaknesses:</p>
              <ul>
                <li>More information on sleep metrics and what they mean were helpful, but some additional information on steps to improve sleep could be even better. Also, it would help to include links to more detailed consumer-grade articles such as those found on WebMD.</li>
                <li>Our proposed new way of charting the sleep score, a waffle chart, was still confusing. While the totals for the scores made more sense, the chart displayed too much info and required users to count.</li>
                <li>The sidebar lacked clarity in how views could be swapped to see different metrics.</li>
              </ul>
              <Image
                  img='./images/earlysense/orig-app.png' 
                  alt='Original Application Path'
                  caption='Original Application Path Analysis'
              />
              <br/>
              <Image
                  img='./images/earlysense/upd-app.png' 
                  alt='Updated Application Path'
                  caption='Modified Application Path Analysis'
              />
            </>
            }
          />

          <ProjectSection
            title="Reflection"
            headinglevel = "2"
            para={
            <>
              <p>There were unique challenges to evaluating an application that is supposed to be paired with a physical device. This was especially challenging for a device that is used during sleep and should be monitored over at least a week to make sense of the results. It was a great experience in trying to balance user-friendliness of the device with trying to provide people with accurate and useful medical information. There was also the challenge of trying to represent complex data with several dimensions in a neat visualization, which I dont believe we fully succeeded in. That being said, the challenges of trying to visualize complex data and explain detailed information simply in this project have helped me improve my data visualization and UX design skills.</p>
            </>
            }
          />



					     
				</div>
	    </section>
    </div>   
  )
}

export default PostEarly;
